.Donut {
  background: #fff;
  color: #779;
  padding: 1rem;
}
.Donut svg {
  margin: 0 auto 1em;
  width: 90%;
}
.Donut dl {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0;
  padding: 0;
  text-align: left;
}
.Donut dt,
.Donut dd {
  border-top: thin solid #c4c4e6;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  padding-top: .5rem;
  padding-bottom: .5rem;
  width: 50%;
}
.Donut dd {
  margin: 0;
}




